<template>

  <div 
    :style="{ '--color': modelValue.color }"
    v-if="modelValue.errors.length" 
    @click="resetError"
    class="error"
  >
    <p 
      v-for="el in modelValue.errors" :key="el" 
      class="error__message" 
    >{{ el }}
    </p>
  </div>

</template>

<script>
import { mapMutations } from 'vuex'
export default {
  name: 'vError',

  props: {
    modelValue: {
      required: true,
      type: Object
    }
  },

  methods: {
    ...mapMutations([
      'resetError'
    ])
  }
}
</script>