<template>
  <div 
    class="carousel-container"
    @mouseup="this.handleOnUp"
    @touchend="this.handleOnUp"
    @mousemove="this.handleOnMove"
    @touchmove="this.handleOnMove"
  >
    <div class="carousel-container__card-track" ref="track" @mousedown="handleOnDown" @touchstart="handleOnDownTouch($event)">
      <v-card 
        v-for="(el, index) in getAllDiscount" :key="index" 
        @detailed="detailed" 
        :el="el"
      ></v-card>
    </div>
  </div>
</template>

<script>
import vCard from '@/scripts/components/vCard.vue'
import { mapGetters } from 'vuex'
import router from '@/router';

export default {
  props: {
    modelValue: {
      required: true,
      type: Number
    }
  },

  data: () => ({
    mouseDownAt: 0,
    prevPercentage: 0,
    percentage: 0
  }),
  
  methods: {
    handleOnDown(e) {
      this.mouseDownAt = e.clientX
    },

    handleOnDownTouch(e) {
      this.mouseDownAt = e.touches[0].clientX
    },

    handleOnUp() {
      this.mouseDownAt = 0
      this.prevPercentage = this.percentage
    },

    handleOnMove(e) {
      if (this.mouseDownAt === 0) return

      const mouseDelta = parseFloat(this.mouseDownAt) - e.clientX
      const maxDelta = window.innerWidth / 2

      const percentage = (mouseDelta / maxDelta) * -100
      const nextPercentageUnconstrained = parseFloat(this.prevPercentage) + percentage
      const nextPercentage = Math.max(Math.min(nextPercentageUnconstrained, 0), -100)

      this.percentage = nextPercentage

      this.$refs.track.animate(
        { transform: `translate(${nextPercentage}%, -50%)` },
        { duration: 5000, fill: "forwards" }
      )

      this.$emit('update:modelValue', nextPercentage )
    },

    detailed( el ) {
      localStorage.setItem( 'current-prod', JSON.stringify( el ) )
      router.push('/catalog')
    }
  },

  computed: {
    ...mapGetters([
      'getAllDiscount'
    ]),
  },

  components: {
    'v-card': vCard
  },

  mounted() {
    
  }
};
</script>