<template>

  <div class="loader" v-if="isLoading">
    <figure>
      <div></div><div></div>
      <div></div><div></div>
      <div></div><div></div>
      <div></div><div></div>
    </figure>
  </div>

  <v-error v-model="getGroupError"></v-error>

  <header class="header" v-if="!isLoading">
    <nav class="header__nav">
      <router-link class="header__nav-link" to="/">Главная</router-link>
      <router-link class="header__nav-link" to="/about">О магазине</router-link>
      <router-link class="header__nav-link" to="/admin" v-if="isAdmin">Админка</router-link>
      <router-link class="header__nav-link" to="/catalog">Каталог</router-link>
      <router-link class="header__nav-link" to="/cart" v-if="getAllProductInCart.length">Корзина</router-link>

      <span class="header__nav-auth">
        <router-link class="header__nav-link" to="/login" v-if="!isAuth">Логин</router-link>
        <router-link class="header__nav-link" to="/registration" v-if="!isAuth">Регистрация</router-link>
      </span>
    </nav>
  </header>
  
  <router-view v-if="!isLoading" />

</template>

<script>
import vError from '@/scripts/components/vError.vue'
import { mapActions, mapGetters, mapMutations } from 'vuex'
import router from './router'

export default {
  name: 'App',

  data: () => ({
    isLoading: true
  }),

  watch: {

  },

  methods: {
    ...mapActions([
      'getAllCategory',
      'getAllAgeLimit',
      'renderCards',
      'getAllCard',
    ]),

    ...mapMutations([
      'setUser',
    ]),
  },

  computed: {
    ...mapGetters([
      'getAllProductInCart',
      'getGroupError',
      'isAdmin',
      'isAuth',
    ]),
  },

  components: {
    'v-error': vError
  },

  created() {
    localStorage.removeItem( 'current-prod' )

    const user = localStorage.getItem( 'user' )
    user && this.setUser( JSON.parse(user) )

    this.getAllCategory()
    this.getAllAgeLimit()
    this.getAllCard()
  },

  mounted() {
    // console.log( window.innerWidth, window.innerHeight ); /* -> w: 1536px h: 778px */

    setTimeout(() => {
      this.isLoading = false
    }, 3000)

    router.push('/')
  }
}
</script>

<style>
  @import "@/styles/css/config.min.css";
</style>
