import { get, post } from '@/scripts/helper.js'

export default {
  actions: {
    getAllCategory: async ({ commit }) => commit( 'setAllCategory', await get( 'http://kkk/public/api/category/all' ) ),
    updateDbCategory: async ({ commit }, data) => {
      const res = await post( 'http://kkk/public/api/category/update', data )
      if( res?.error ) commit( 'setError', res.error );
      commit( 'setAllCategory', res.categories )
      commit( 'setAllCard', res.products )
    },

    deleteCategoryInDb: async ({ commit }, data) => {
      const res = await post( 'http://kkk/public/api/category/delete', data )
      if( res?.error ) commit( 'setError', res.error );
      if( res?.categories ) commit( 'setAllCategory', res?.categories )
      if( res?.products ) commit( 'setAllCard', res?.products )
    },

    getAllAgeLimit: async ({ commit }) => commit( 'setAllAgeLimit', await get( 'http://kkk/public/api/agelimit/all' ) ),
    updateDbAgeLimit: async ({ commit }, data) => {
      const res = await post( 'http://kkk/public/api/agelimit/update', data )
      if( res?.error ) commit( 'setError', res.error );
      commit( 'setAllAgeLimit', res.agelimits )
      commit( 'setAllCard', res.products )
    },

    deleteAgeLimitInDb: async ({ commit }, data) => {
      const res = await post( 'http://kkk/public/api/agelimit/delete', data )
      if( res?.error ) commit( 'setError', res.error );
      if( res?.agelimits ) commit( 'setAllAgeLimit', res?.agelimits )
      if( res?.products ) commit( 'setAllCard', res?.products )
    },

    renderCards: async ({ commit }, data) => commit( 'setAllCard', await post( 'http://kkk/public/api/products/create', data ) ),
    getAllCard: async ({ commit }) => commit( 'setAllCard', await get( 'http://kkk/public/api/products/all' ) ),
    updateDbProduct: async ({ commit }, data) => {
      const res = await post( 'http://kkk/public/api/products/update', data )
      if( res?.error ) commit( 'setError', res.error );
      commit( 'setAllCard', res.products )
    },

    deleteProductInDb: async ({ commit }, data) => {
      const res = await post( 'http://kkk/public/api/products/delete', data )
      if( res?.error ) commit( 'setError', res.error );
      if( res?.products ) commit( 'setAllCard', res.products )
      if( res?.error ) commit( 'setError', res.error );
    },
  },

  mutations: {
    setAllCard( state, newArr ) {
      state.products = newArr
    },

    setAllCategory( state, newArr ) {
      state.category = newArr
    },

    setAllAgeLimit( state, newArr ) {
      state.ageLimit = newArr
    },

    addProdInCart( state, newProd ) {
      state.cart.push( newProd )
    },

    clearCart( state ) {
      state.cart = []
    }
  },

  state: {
    products: [],
    category: [],
    ageLimit: [],
    cart: []
  },

  getters: {
    getAllProductInCart( state ) {
      return state.cart
    },

    getAllAgeLimit( state ) {
      return state.ageLimit
    },

    getAllCategory( state ) {
      return state.category
    },

    getAllDiscount( state ) {
      return state.products.slice( 0, 7 )
    },

    getAllProduct( state ) {
      return state.products
    },
  },
  
}